	display: flex;
	flex-direction: column;

	align-items:  center;
	order:1;
	margin-top: 20px;



}

.appInfoContainer img{
	height:278px;
}


.Auth{
	margin-top:20px;
	width:100%;
	text-align: center;
	/*box-shadow:0 2px 3px #ccc;*/
	/*border: 1px solid #eee;*/
	padding:10px;
	box-sizing: border-box;
	order:2;

}

.heading{

	font-size: 30px;
